import React from 'react';
import {
    Navbar,
    Nav
} from 'react-bootstrap';
import {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton,
  TelegramShareButton,
  WhatsappShareButton,
  PinterestShareButton,
  VKShareButton,
  OKShareButton,
  RedditShareButton,
  TumblrShareButton,
  LivejournalShareButton,
  MailruShareButton,
  ViberShareButton,
  WorkplaceShareButton,
  EmailShareButton,
} from 'react-share';

import {
  FacebookIcon,
  TwitterIcon,
  TelegramIcon,
  WhatsappIcon,
  LinkedinIcon,
  PinterestIcon,
  VKIcon,
  OKIcon,
  RedditIcon,
  TumblrIcon,
  LivejournalIcon,
  MailruIcon,
  ViberIcon,
  WorkplaceIcon,
  EmailIcon,
} from 'react-share';

const Layout = props => (
    <div >
        <Navbar bg="dark" variant="dark">
            <Navbar.Brand href={process.env.PUBLIC_URL+"/"}>COMMUNICATIONS STYLE INVENTORY</Navbar.Brand>
            <Nav className="mr-auto">
                {/* <Nav.Link href={"/"}>Home</Nav.Link>
                <Nav.Link href={"/about"}>About</Nav.Link>
                <Nav.Link href={"/questions"}>Questions</Nav.Link> */}
                <Nav.Link href={process.env.PUBLIC_URL+"/"}>Home</Nav.Link>
                <Nav.Link href={process.env.PUBLIC_URL+"/about"}>About</Nav.Link>
                <Nav.Link href={process.env.PUBLIC_URL+"/questions"}>Questions</Nav.Link>
            </Nav>
        </Navbar>
        {props.children}
        <Navbar bg="dark" variant="dark" fixed="bottom">
          <FacebookShareButton url={window.location}>
            <FacebookIcon size={32} round={true} />
          </FacebookShareButton>

          <LinkedinShareButton url={window.location} >
            <LinkedinIcon size={32} round={true} />
          </LinkedinShareButton>

          <TwitterShareButton url={window.location} > 
            <TwitterIcon size={32} round={true} />
          </TwitterShareButton>

          <TelegramShareButton url={window.location} > 
            <TelegramIcon size={32} round={true} />
          </TelegramShareButton>

          <WhatsappShareButton url={window.location} >  
            <WhatsappIcon size={32} round={true} />
          </WhatsappShareButton>

          <PinterestShareButton url={window.location} >  
            <PinterestIcon size={32} round={true} />
          </PinterestShareButton>

          <VKShareButton url={window.location} >  
            <VKIcon size={32} round={true} />
          </VKShareButton>

          <OKShareButton url={window.location} >  
            <OKIcon size={32} round={true} />
          </OKShareButton>

          <RedditShareButton url={window.location} >  
            <RedditIcon size={32} round={true} />
          </RedditShareButton>

          <TumblrShareButton url={window.location} >  
            <TumblrIcon size={32} round={true} />
          </TumblrShareButton>

          <LivejournalShareButton url={window.location} >  
            <LivejournalIcon size={32} round={true} />
          </LivejournalShareButton>

          <MailruShareButton url={window.location} >  
            <MailruIcon size={32} round={true} />
          </MailruShareButton>

          <ViberShareButton url={window.location} >  
            <ViberIcon size={32} round={true} />
          </ViberShareButton>

          <WorkplaceShareButton url={window.location} >  
            <WorkplaceIcon size={32} round={true} />
          </WorkplaceShareButton>

          <EmailShareButton url={window.location} >  
            <EmailIcon size={32} round={true} />
          </EmailShareButton>
        </Navbar>
    </div>
  );
  
export default Layout;
  