import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  Paper,
  ListItem,
  List,
  Link
} from '@material-ui/core';

function TakeTheQuizSection(props) {

  return (
    <Card variant="outlined">
        <CardContent>
        <Link
          variant="body3"
          href={`${process.env.PUBLIC_URL}/questions`}
        >
          Take the Quiz
        </Link>
        </CardContent>
    </Card>
  )
}

function ResultOutput(props) {
  let bulletList  = props.resultInfo.bullets.map((bullet, index) => {
    return (
      <ListItem key={index} divider>
        {bullet}
      </ListItem>
    )
  })

  return (
    <Card variant="outlined">
        <CardContent>
          <Typography color="textSecondary">
          {`${props.resultInfo.type}`}
          </Typography>
          <List>
            {bulletList}
          </List>
          
        </CardContent>
    </Card>
  )
}

function Results({communicationStyleData}) {

  let search = window.location.search;
  let params = new URLSearchParams(search);
  let data = params.get('data');

  let dataJSON = validatedData(data)
  let keyCount = [
    {"key": "O", "count": getCount("O", dataJSON.answers)},
    {"key": "G", "count": getCount("G", dataJSON.answers)},
    {"key": "D", "count": getCount("D", dataJSON.answers)},
    {"key": "I", "count": getCount("I", dataJSON.answers)}
  ].sort((a, b) => b.count - a.count)

  let resType = [keyCount[0].key, keyCount[1].key].sort().join("")

  function validatedData(data){
    let dataJSON = {}
    try {
      dataJSON = JSON.parse(atob(data))
    } catch(err){
      console.log(err)
    }

    return dataJSON
  }

  function getCount(key, answers) {
    let count = 0;
    let expectedAnswers = communicationStyleData.groupResults[key]
    for(let question in expectedAnswers){
      if(expectedAnswers[question] === answers[question]){
        count++;
      }
    }

    return count;
  }

  console.log(communicationStyleData.resultInfos)
  console.log(resType)
  console.log(communicationStyleData.resultInfos[resType])

  return (
    <Paper elevation={3}>
        <TakeTheQuizSection />
        <Card variant="outlined">
            <CardContent>
            <Typography variant="h4" component="h2">
              {`Results for: ${dataJSON.name || "Unknown"}`}
            </Typography>
            </CardContent>
        </Card>
        <ResultOutput resultInfo={communicationStyleData.resultInfos[resType]}/>
        {/* <Card variant="outlined">
            <CardContent>
            <Typography color="textSecondary">
            {`${JSON.stringify(keyCount)}`}
            </Typography>
            </CardContent>
        </Card> */}
        <TakeTheQuizSection />
    </Paper>
  );
}

export default Results;
